*,
*::before,
*::after {
  box-sizing: border-box; }

html {
  font-size: 16px; }

body {
  font-family: 'Lato', Arial, sans-serif;
  font-weight: 400;
  color: #21262D;
  overflow-wrap: break-word;
  word-wrap: break-word; }

a,
a:visited {
  text-decoration: none; }

.container {
  min-width: 320px;
  max-width: 1030px;
  padding: 0 15px;
  box-sizing: border-box;
  margin: 0 auto; }

/* blocks */
.header {
  padding: 40px 0; }

.nav {
  display: flex;
  justify-content: space-between; }
  .nav__logo {
    transition: all 0.1s linear; }
    .nav__logo:hover {
      transform: scale(1.03); }
  .nav__menu {
    display: flex;
    align-items: center; }
  .nav__link {
    position: relative;
    font-size: 0.9375rem;
    line-height: 1;
    color: #21262D;
    transition: color 0.1s ease-out; }
    .nav__link::before {
      content: '';
      position: absolute;
      top: -5px;
      left: 100%;
      width: 0;
      height: 2px;
      background: #FC033D;
      transition: background 0.1s linear, width 0.1s linear, left 0.1s linear; }
    .nav__link::after {
      content: '';
      position: absolute;
      bottom: -6px;
      left: 0;
      width: 0;
      height: 2px;
      background: #FC033D;
      transition: background 0.1s linear, width 0.1s linear, left 0.1s linear; }
    .nav__link:hover:not(.nav__link--join) {
      color: #FC033D; }
      .nav__link:hover:not(.nav__link--join)::before {
        width: 100%;
        left: 0; }
      .nav__link:hover:not(.nav__link--join)::after {
        width: 100%;
        left: 0; }
    .nav__link:not(.nav__link--join) {
      margin-right: 30px; }
    .nav__link--join {
      background: #16B2FB;
      padding: 9px 40px;
      color: #ffffff;
      border-radius: 5px;
      height: 33px;
      margin-right: 15px;
      transition: background 0.1s; }
      .nav__link--join:hover {
        background: #0496da; }
  .nav__languages {
    border: 1px solid #D3DBE9;
    border-radius: 5px;
    padding: 8px 13px;
    color: #95A0B4;
    cursor: default; }

.languages {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 33px;
  height: 33px; }
  .languages__current {
    width: 18px;
    text-align: center; }
  .languages__list {
    max-height: 0;
    position: absolute;
    left: 0;
    top: 100%;
    width: 100%;
    overflow: hidden;
    transition: max-height 0.4s linear; }
  .languages:hover .languages__list {
    max-height: 300px; }
  .languages__item {
    display: block;
    padding: 4px 0;
    border-bottom: 1px solid #D3DBE9;
    color: #95A0B4;
    text-align: center;
    transition: 0.1s linear; }
    .languages__item:hover {
      color: #FC033D; }
    .languages__item:last-child {
      border-bottom: 0; }

.hero {
  position: relative;
  margin-top: 60px;
  background: #F8F8F8; }
  .hero::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 70px;
    background: #ffffff;
    top: 0;
    left: 0;
    z-index: 1; }
  .hero__container {
    display: flex; }
  .hero__message, .hero__img {
    z-index: 2; }
  .hero__message {
    max-width: 540px; }
  .hero__title {
    max-width: 275px;
    font-size: 3rem;
    font-weight: bold;
    line-height: 3.374rem;
    margin: 2.5rem 0 1.75rem; }
  .hero__description {
    font-size: 1.25rem;
    line-height: 2rem;
    font-weight: 400;
    margin-bottom: 3rem; }
  .hero__img {
    font-size: 0;
    margin-left: auto; }
  .hero__button {
    border: 0;
    appearance: none;
    box-shadow: 0 1px 5px rgba(0, 0, 0, 0.05);
    transition: all 0.1s ease; }
    .hero__button--red {
      background: #FC033D;
      color: #ffffff;
      padding: 13px 54px 15px;
      border-radius: 5px; }
      .hero__button--red:hover {
        background: #ca0231; }
    .hero__button--white {
      position: relative;
      left: -20px;
      background: #ffffff;
      color: #FC033D;
      padding: 8px 54px 10px;
      border-radius: 5px; }
      .hero__button--white:hover {
        padding: 8px 45px 10px;
        left: -11px;
        box-shadow: 0 1px 5px rgba(0, 0, 0, 0.15); }

.about__container {
  position: relative;
  background: url("../img/about-bg.png") left top no-repeat; }

.about__title {
  margin: 0 0 60px;
  padding-top: 75px;
  font-size: 1.25rem;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center; }

.about__content {
  display: flex; }

.about__info {
  flex: 1;
  max-width: 480px; }

.about__description {
  max-width: 380px;
  font-size: 1rem;
  font-weight: 300;
  margin: 0 0 60px;
  line-height: 1.5rem; }

.about__list {
  list-style: none;
  margin-left: 1.5rem;
  padding: 0; }

.about__item {
  position: relative;
  font-size: 1.125rem;
  font-weight: 300;
  margin-bottom: 25px;
  line-height: 1.5rem; }
  .about__item:last-child {
    margin-bottom: 0; }
  .about__item::before {
    content: '';
    position: absolute;
    background: #FC033D;
    display: inline-block;
    width: 11px;
    height: 11px;
    top: 8px;
    margin-left: -24px;
    border-radius: 50%; }

.cards {
  max-width: 360px;
  margin-left: auto;
  background: url("../img/cards-bg.png") center no-repeat; }
  .cards__item {
    display: flex;
    align-items: center; }
    .cards__item:first-child {
      margin-bottom: 1.5rem; }
    .cards__item:hover .cards__img {
      transform: translateY(-10px) rotate3d(1, 1, 1, 3deg); }
  .cards__description {
    padding: 25px 0;
    font-size: 0.9rem;
    font-weight: 300;
    line-height: 1.5rem; }
  .cards__digit {
    display: block;
    font-size: 1.25rem;
    margin-bottom: 0.25rem; }
  .cards__img {
    transition: 0.2s ease-in-out; }
    .cards__img--left {
      margin-right: 20px; }
    .cards__img--right {
      margin-left: 20px; }
